@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(180deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes opacity0to0 {
  from {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@mixin font($size, $weight) {
  font-size: $size;
  font-weight: $weight;
}

@mixin absolute($top: initial, $right: initial, $bottom: initial, $left: initial, $width: 24px, $height: 24px) {
  position: absolute;
  top: $top;
  right: $right;
  bottom: $bottom;
  left: $left;
  width: $width;
  height: $height;
}

@mixin flex($flex-direction: row, $justify-content: flex-start, $align-items: center) {
  display: flex;
  flex-direction: $flex-direction;
  justify-content: $justify-content;
  align-items: $align-items;
}

@mixin responsiveWidth() {
  width: 1440px;
  margin: 0 auto;
  @media screen and (max-width: 1440px) {
    width: 100%;
    padding-right: 24px;
    padding-left: 24px;
  }
}

@mixin scrollBar($type: vertical, $wh: 6px, $track-color: $c2, $thumb-color: $c1) {
  &::-webkit-scrollbar {
    width: $wh;
    height: $wh;
  }
  &::-webkit-scrollbar-track {
    background: $track-color;
  }
  &::-webkit-scrollbar-thumb {
    background: $thumb-color;
    border-radius: 9999px;
  }
  @if ($type == vertical) {
    overflow-x: hidden;
    overflow-y: scroll;
  } @else {
    overflow-x: scroll;
    overflow-y: hidden;
  }
}

@mixin textEllipsis($line-clamp: 2) {
  overflow: hidden;
  text-overflow: ellipsis;
  @if ($line-clamp >= 2) {
    display: -webkit-box;
    -webkit-line-clamp: $line-clamp;
    -webkit-box-orient: vertical;
  } @else {
    display: block;
    white-space: nowrap;
  }
}

@mixin nullImg($text: "이미지없음", $object-fit: cover, $aspect-ratio: 3/4, $bg: $c4, $color: $c1) {
  position: relative;
  object-fit: $object-fit;
  aspect-ratio: $aspect-ratio;
  &[src="null"] {
    &::before {
      content: "";
      @include absolute($left: 0, $top: 0, $width: 100%, $height: 100%);
      display: block;
      background-color: $bg;
    }
    &::after {
      content: $text;
      @include absolute($left: 50%, $top: 50%, $width: initial, $height: initial);
      @include font($small, $x-bold);
      white-space: nowrap;
      transform: translate(-50%, -50%);
      color: $color;
    }
  }
}
